import React, {useState, useEffect, Fragment} from 'react';

import AddressItem from '../components/AddressItem';
import Product from '../components/Product';
import ProductLarge from '../components/ProductLarge';
import LeftSidebar from '../components/LeftSidebar';
import CartItem, {getCartItems} from '../components/CartItem';
import ConfirmOrder from '../components/ConfirmOrder';

import {slide as Menu} from 'react-burger-menu';
import Toggle from 'react-toggle';
import {Dropdown, DropdownButton, Button, Badge} from 'react-bootstrap';
import {FaShoppingBasket} from 'react-icons/fa';

import {
  BrowsingWrapper, ProductsButtonWrapper, IconWrapper, CartContainer, CartWrapper,
  TitleContainer, TitleWrapper, BadgeWrapper
} from '../jsx/Order.jsx';
import { 
  TitleTextGreen , HomeWrapper, 
  ScaleWrapper, ScaleInfoWrapper, ScaleText, ScaleTextWrapper,
  InnerContainer, WeightLabelLeft, WeightLabelRight, 
} from '../jsx/App.jsx';

import '../css/bootstrap.min.css';

const productsTemplate = [
  {
    "id" : 1,
    "name" : "Blue Dream 1/8 oz",
    "description" : "Hybrid",
    "longDescription": "This high quality leaf is meant to give you a calm, relaxing high. This will give you a focused yet calm high throughout the day.",
    "measurement" : "24% THC",
    "price" : {dollars: 40, cents: 0},
    "type" : "flower",
    "imageURL" : "https://media.phillyvoice.com/media/images/01_030817_Dispensary_Carroll.2e16d0ba.fill-735x490.jpg"
  },
  {
    "id" : 2,
    "name" : "Nathan's Brownies: Macadamia Nut",
    "description" : "Edible",
    "longDescription": "This tasty Brownie is guaranteed to give you a calm, sleepy high. This will help you get a nice, deep sleep.",
    "measurement" : "100mg",
    "price" : {dollars: 19, cents: 99},
    "type" : "edible",
    "imageURL" : "https://thekitchenpaper.com/wp-content/uploads/2013/04/macadamia-mocha-brownies3.jpg"
  }
];

const AddressItems = [
  {
  "id" : "1",
  "name" : "Rolling Greens",
  "address" : "20 East Street",
  "phone" : "555-248-2459",
  "rating" : 4.5,
  "numOfRatings" : 100,
  "imageURL" : "https://media.timeout.com/images/102035297/image.jpg"
  },
  {
    "id": "2",
    "name" : "Hamilton's Goods",
    "address" : "55 West Road",
    "phone" : "555-248-2459",
    "rating" : 3.5,
    "numOfRatings" : 200,
    "imageURL" : "https://www.tiffany.com/shared/images/Stores/Chevy_Chase.jpg"
    },
];

const Order = () => {
    const [currLocation, setLocation] = useState({name:"Set Location"});
    const [currProduct, setCurrProduct] = useState(null);
    const [productSelected, setProductSelected] = useState(false);
    const [productsFilter, setProductsFilter] = useState("All Products");
    const [filteredProducts, setFilteredProducts] = useState(null);
    const [loading, setLoading] = useState(true);
    const [locationOptions, setLocationOptions] = useState(null);
    const [products, setProducts] = useState(null);
    const [cartItems, setCartItems] = useState({});
    const [cartCount, setCartCount] = useState(0);
    const [showCart, setShowCart] = useState(false);
    const [cartTotal, setCartTotal] = useState({dollars: 0, cents: 0});
    const [shouldConfirmOrder, setShouldConfirmOrder] = useState(false);
    const [orderError, setOrderError] = useState("");
    const [userData, setUserData] = useState({});
    const [orderConfirmed, setOrderConfirmed] = useState(false);

    const getLocationOptions = () => {
      setLocationOptions(AddressItems);
      setLoading(false);
    };
    const updateLocation = (location) => {
      setShowCart(false);
      setShouldConfirmOrder(false);
      if(cartCount!=0){
        if(location.id === currLocation.id){
          setFilteredProducts(products);
          setProductSelected(false);
          setCurrProduct(null);
          return;
        }
        setCartItems({});
      }
      setLocation(location);
      getProducts(location);
    };
    const getProducts = (location) => {
      setProducts(productsTemplate);
      setFilteredProducts(productsTemplate);
      setCurrProduct(null);
      setProductSelected(false);
    };
    const updateProductFilter = (selected) => {
      if(selected==="All Products"){
        setFilteredProducts(products);
      }
      else if(selected==="Flower"){
        let filteredItems = products.filter((item)=>item.type=="flower");
        setFilteredProducts(filteredItems);
      }
      else if(selected==="Non-Flower"){
        let filteredItems = products.filter((item)=>item.type!=="flower");
        setFilteredProducts(filteredItems);
      }
      else if(selected==="Miscellaneous"){
        let filteredItems = products.filter((item)=>(item.type!=="flower"&&item.type!=="edible"))
      }
      setProductsFilter(selected);
    };
    const updateProducts = (selected) => {
      setCurrProduct(selected);
    };
    const updateSelectedProduct = (product) => {
      if(product==null){
        setProductSelected(false);
        setCurrProduct(null);
        updateProductFilter("All Products");
      }
      setProductSelected(true);
      setCurrProduct(product);
    }
    const updateQuantity = (amount, itemID) => {
      let updatedItems = {...cartItems};
      updatedItems[itemID].quantity = ((updatedItems[itemID].quantity+amount) >= 0) ? updatedItems[itemID].quantity+amount : 0;
      
      let newTotal = {};
      newTotal.dollars = (cartTotal.dollars + (updatedItems[itemID].price.dollars * amount));
      let updateCents = cartTotal.cents + (updatedItems[itemID].price.cents * amount);
      if(updateCents<0){
        newTotal.dollars = newTotal.dollars - (Math.ceil(Math.abs(updateCents) / 100));
        updateCents = updateCents + (100 * (Math.floor(Math.abs(updateCents) / 100)));
        if(updateCents<0){
          updateCents = 100 + updateCents;
        }
      }
      else if(updateCents>=100){
        newTotal.dollars = newTotal.dollars + 1;
        updateCents = updateCents%100;
      }
      newTotal.cents = updateCents;

      let updatedCartCount = ((cartCount+amount) > 0) ? (cartCount+amount) : 0;

      if(updatedItems[itemID].quantity === 0){
        delete updatedItems[itemID];
      }

      setCartCount(updatedCartCount);
      setCartItems(updatedItems);
      setCartTotal(newTotal);
    }
    const buyProduct = (product) => {
      let newTotal = {};
      newTotal.dollars = (cartTotal.dollars + product.price.dollars);
      let updateCents = cartTotal.cents + product.price.cents;
      if(updateCents>=100){
        newTotal.dollars = newTotal.dollars + 1;
        newTotal.cents = updateCents % 100;
      }
      else{
        newTotal.cents = updateCents;
      }

      setCartCount(cartCount+1);
      setCartTotal(newTotal);
      
      let currItem = {...product};
      let currID = currItem.id;
      if(cartItems[currID]!=null){
        updateQuantity(1,currID);
      }
      else{
        let newcartItem = {[currID] : {...product, quantity: 1}};
        setCartItems({...cartItems, ...newcartItem});
      }
    }
    const removeProduct = (productID) => {
      let quantity = (cartItems[productID].quantity * -1);
      updateQuantity(quantity, productID);
    }
    const confirmOrder = () => {
      setShouldConfirmOrder(true);
    }
    const submitOrder = (formUserData) => {
      if(formUserData.name && formUserData.tel &&
        formUserData.name.length > 3 && formUserData.tel.length > 9
        ){

        // on success
        setOrderError("");
        setCartCount(0);
        setCartItems({});
        setUserData({});
        setCartTotal({dollars: 0, cents: 0});
        setProductSelected(false);
        setCurrProduct(null);
        setOrderConfirmed(true);
      }
      else{
        setOrderError("Please enter your contact information.");
      }
    }
    const confirmComplete  = () => {
      setOrderConfirmed(false);
      setShouldConfirmOrder(false);  
      setShowCart(false);
    }

    useEffect(()=>{
      getLocationOptions();
    }, []);

    const getDropDowns = (locationData) => {
      return(
        <Dropdown.Item onClick={()=>{updateLocation(locationData)}}>{locationData.name}</Dropdown.Item>
      );
    }


    return (
      <HomeWrapper>
        <InnerContainer>
            <LeftSidebar hideWeightOption = {true} />
        </InnerContainer>
        <TitleWrapper>
          
          <TitleContainer>
            {/* <a href="/"><TitleTextGreen href="/">Set Location</TitleTextGreen></a> */}
            {
              (!showCart) 
              ?
              <Dropdown>
                <Dropdown.Toggle variant="success" id="dropdown-basic">
                  {currLocation.name}
                </Dropdown.Toggle>

                <Dropdown.Menu> 
                  {
                    (loading || locationOptions==null)
                    ?
                    <Dropdown.Item onClick={()=>{updateLocation({name: "Default"})}}>Default</Dropdown.Item>
                    :
                    <Fragment>
                      {locationOptions.map(item => getDropDowns(item))}
                    </Fragment>
                  }
                  
                </Dropdown.Menu>
              </Dropdown>
              :
              <Button variant="success" onClick = {()=>{setShowCart(false)}}>Continue Shopping</Button>  
            }
          </TitleContainer>
          <CartWrapper>
            <CartContainer onClick={()=>{setShowCart(true)}}>
              <IconWrapper><FaShoppingBasket /></IconWrapper>
              <BadgeWrapper>
                <Badge variant="danger">
                  {cartCount}
                </Badge>
              </BadgeWrapper>
            </CartContainer>
          </CartWrapper>
        </TitleWrapper>
        <BrowsingWrapper>
          {
            (currLocation && (currLocation.name === "Set Location" || currLocation.name === "Default"))
            ? 
            AddressItems.map((item)=>AddressItem(item, updateLocation))
            :
            AddressItem(currLocation,updateLocation)
          }
          {
            (products && products.length>0 && currLocation != "Set Location" && !productSelected && !showCart)
            &&
              <Fragment>
                <ProductsButtonWrapper>
                  <Dropdown>
                    <Dropdown.Toggle variant="success" id="dropdown-basic">
                      {productsFilter}
                    </Dropdown.Toggle>

                    <Dropdown.Menu>
                      <Dropdown.Item onClick={()=>{updateProductFilter("All Products")}}>All Products</Dropdown.Item>
                      <Dropdown.Item onClick={()=>{updateProductFilter("Flower")}}>Flower</Dropdown.Item>
                      <Dropdown.Item onClick={()=>{updateProductFilter("Non-Flower")}}>Non-Flower</Dropdown.Item>
                      <Dropdown.Item onClick={()=>{updateProductFilter("Miscellaneous")}}>Miscellaneous</Dropdown.Item>
                    </Dropdown.Menu>
                  </Dropdown>
                </ProductsButtonWrapper>
                  {filteredProducts.map((item)=>Product(item, updateSelectedProduct))}

              </Fragment>
              
          }
          {
            (productSelected && currProduct!=null && !showCart)
            &&
              <Fragment>
                
                  {ProductLarge(currProduct, buyProduct)}

              </Fragment>
              
          }
          {
            (showCart && !shouldConfirmOrder)
            ?
            <Fragment>
              {getCartItems(cartItems, cartCount, cartTotal, setCartItems, updateQuantity, removeProduct, confirmOrder)}
            </Fragment>
            :
            (shouldConfirmOrder) &&
            <Fragment>
              {ConfirmOrder(cartCount, cartTotal, confirmOrder, submitOrder, orderError, userData, orderConfirmed, confirmComplete)}
            </Fragment>
          }
        </BrowsingWrapper>
      </HomeWrapper>
    )
}

export default Order;
