import {MenuStyles} from './MenuStyles.jsx';
import {slide as Menu} from 'react-burger-menu';
import { 
    WeightLabelLeft, WeightLabelRight
  } from '../jsx/App.jsx';

import Toggle from 'react-toggle';

const LeftSidebar = (props) => {
    return(
        <Menu id={ "sidebar" } styles={MenuStyles} disableAutoFocus={true}>
            <br/>
            <a id="home" className={MenuStyles.bmItem} href="/Home">Home</a>
            <a id="settings" className={MenuStyles.bmItem} href="/">Settings</a>

            { 
            !props.hideWeightOption && 
            <label className={MenuStyles.bmItem}>
                <WeightLabelLeft>Kg </WeightLabelLeft>
                <Toggle
                    defaultChecked={true}
                    icons={false}
                    onChange={props.handleWeightChange} />
                <WeightLabelRight> Grams</WeightLabelRight>
            </label>
            }
            <a id="order" className={MenuStyles.bmItem} href="/order">Create an Order</a>
            <a id="about" className={MenuStyles.bmItem} href="/about">State Laws</a>
            <a id="partners" className={MenuStyles.bmItem} href="/">Digiscale Partners</a>
        </Menu>
    )
}
export default LeftSidebar;