const Product = (props, updateSelectedProduct) => {
    return(
      <Container onClick={()=> updateSelectedProduct(props)}>
        <ImageContainer>
          <Image src={props.imageURL} />
        </ImageContainer>
        <InfoContainer>
          {`$${props.price.dollars}.` + (props.price.cents<10 ? `0` : ``) + `${props.price.cents}` } 
          <br/>
          {props.name}
        </InfoContainer>
        <InfoContainer>
          {props.measurement} 
          <br/>
          {props.description}
        </InfoContainer>
      </Container>
    );
}
export default Product;