import React, {useState} from 'react';
import { 
  TitleTextRed, TitleContainer , TitleWrapper, HomeWrapper, 
  ScaleWrapper, ScaleInfoWrapper, ScaleText, ScaleTextWrapper,
  InnerContainer, WeightLabelLeft, WeightLabelRight
} from '../jsx/App.jsx';

import LeftSidebar from '../components/LeftSidebar';
import Pressure from 'react-pressure';

const configs = {
  polyfill: false, //Only people with 3D touch
  only: "touch",    //Only Iphones 
  preventSelect: true,
  polyfillSpeedUp: 10
}

class Home extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      weightType: "grams"
    }
  }
  setWeightType = (type) =>  {
    this.setState({weightType:type});
  }
  handleWeightChange = () =>{
    if(this.state.weightType==="kg"){
      this.setWeightType("grams");
    }
    else {
      this.setWeightType("kg");
    }
  }
  render(){
    return (
      <HomeWrapper>
        <InnerContainer>
            <LeftSidebar handleWeightChange={this.handleWeightChange}/>
        </InnerContainer>
        <TitleWrapper>
          <TitleContainer>
            <a href="/"><TitleTextRed href="/">Self Destruct</TitleTextRed></a>
          </TitleContainer>
        </TitleWrapper>
        <ScaleWrapper>
          
        </ScaleWrapper>
        <ScaleInfoWrapper>
          <ScaleTextWrapper>
            
            <ScaleText>
              {(this.state.weightType == "grams") ?
              Number((this.props.force*30).toFixed(3))+" "+this.state.weightType
              :
              Number((this.props.force*0.03).toFixed(6))+" "+this.state.weightType
              }
            </ScaleText>
          </ScaleTextWrapper>
        </ScaleInfoWrapper>
      </HomeWrapper>
    )
  }
}

export default Pressure(Home, configs);
