import React, {useState} from 'react';
import { 
  TitleText, TitleContainer , TitleWrapper, SubtitleText,SubtitleTextGreen, SubtitleContainer, ConfirmWrapper, 
  ScaleWrapper, ScaleInfoWrapper, ButtonContainer, ButtonContainerRed, ButtonWrapper, ErrorContainer,
  Container, SignUpContainer
} from '../jsx/Confirm.jsx';
import { useHistory } from 'react-router-dom';

const Confirm = (props) =>{
  const history = useHistory();
  const [fullName, setfullName] = useState("");
  const [IDNumber, setIDNumber] = useState("");
  const [EIN, setEIN] = useState("");
  const [address, setAddress] = useState("");
  const [state, setState] = useState("");
  const [cityCounty, setCityCounty] = useState("");
  const [zip, setZip] = useState("");
  const [email, setEmail] = useState("");
  const [signedUp, setSignedUp] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  
  const handleSignUp = () => {
   
    if(fullName.length<3){
        setErrorMessage("Full Name must be longer.")
    }
    else if(IDNumber.length<5){
        setErrorMessage("IDNumber not long enough.")
    }
    else if(EIN.length<8){
        setErrorMessage("SSN/EIN must be 9 digits or longer.");
    }
    else if(email.length<3){
        setErrorMessage("Email less than minimum length");
    }
    else{
        setSignedUp(true);
        // history.push('/Home');
    }
  }
  return (
    <ConfirmWrapper>
        <TitleWrapper>
            <TitleContainer>
            <TitleText>Digiscale</TitleText>
            </TitleContainer>
        </TitleWrapper>
        {
            (props.ageConfirmed) ?
            (    
              (signedUp) ?
              <Container>
                <SubtitleContainer>
                    <SubtitleText></SubtitleText>
                </SubtitleContainer>
                <SubtitleContainer>
                    <SubtitleText>
                      Once the background check is complete, we will send you an email with an update of your status.
                      <br/><br/>
                      A confirmation number will be included if approved. 
                      <br/><br/>
                      Simply click on the Digiscale icon, then input the number to gain access to available pickups in your local area.
                    </SubtitleText>
                </SubtitleContainer>
                <ButtonWrapper>
                    <ButtonContainer>
                        <a onClick={()=>{history.push('/Home')}}>Continue</a> 
                    </ButtonContainer>
                </ButtonWrapper>
              </Container>
              :
              <Container>
                <SubtitleContainer>
                    <SubtitleTextGreen>Are you over 21 years of age?</SubtitleTextGreen>
                </SubtitleContainer>

                <SignUpContainer>
                    <input
                        type={"text"}
                        value={fullName}
                        placeholder={"First and Last Name"}
                        onChange={(event)=>{setfullName(event.target.value)}}
                    />
                </SignUpContainer>
                <SignUpContainer>
                    <input
                        type={"text"}
                        value={IDNumber}
                        placeholder={"License/ID Number"}
                        onChange={(event)=>{setIDNumber(event.target.value)}}
                    />
                </SignUpContainer>
                <SignUpContainer>
                    <input
                        type={"number"}
                        value={EIN}
                        placeholder={"SSN/EIN"}
                        onChange={(event)=>{setEIN(event.target.value)}}
                    />
                </SignUpContainer>
                <SignUpContainer>
                    <input
                        value={address}
                        placeholder={"Address"}
                        onChange={(event)=>{setAddress(event.target.value)}}
                    />
                </SignUpContainer>
                <SignUpContainer>
                    <input
                        value={state}
                        placeholder={"State"}
                        onChange={(event)=>{setState(event.target.value)}}
                    />
                </SignUpContainer>
                <SignUpContainer>
                    <input
                        value={cityCounty}
                        placeholder={"City/County"}
                        onChange={(event)=>{setCityCounty(event.target.value)}}
                    />
                </SignUpContainer>
                <SignUpContainer>
                    <input
                        value={zip}
                        placeholder={"Zip Code"}
                        onChange={(event)=>{setZip(event.target.value)}}
                    />
                </SignUpContainer>
                <SignUpContainer>
                    <input
                        value={email}
                        placeholder={"Email Address"}
                        onChange={(event)=>{setEmail(event.target.value)}}
                    />
                </SignUpContainer>
                <ErrorContainer>
                    <p>{errorMessage}</p>
                </ErrorContainer>
                <ButtonWrapper>
                    <ButtonContainer>
                        <a onClick={()=>{handleSignUp()}}>Sign Up</a> 
                    </ButtonContainer>
                </ButtonWrapper>
                <ButtonWrapper>
                    <ButtonContainerRed>
                        <a onClick={()=>{history.push('/Home')}} >Skip</a> 
                    </ButtonContainerRed>
                </ButtonWrapper>
              </Container>
            )
            : 
            <Container>
                <SubtitleContainer>
                    <SubtitleTextGreen>Are you over 21 years of age?</SubtitleTextGreen>
                </SubtitleContainer>
                <ButtonWrapper>
                    <ButtonContainer>
                        <button onClick={()=>{props.setAgeConfirmed(true)}}>Yes</button> 
                    </ButtonContainer>
                </ButtonWrapper>
                <ButtonWrapper>
                    <ButtonContainer>
                        <button href="https://google.com" onClick={()=>props.setAgeConfirmed(false)}>No</button> 
                    </ButtonContainer>
                </ButtonWrapper>
            </Container>
        }
        
    </ConfirmWrapper>
  )
}

export default Confirm;
