import React, {Fragment, useState} from 'react';
import {
    CartList, ListItem, CartTitle, RedIconWrapper, GreenIconWrapper,
    CartImage, InfoContainer, CartTotalContainer
} from '../components-jsx/CartItem.jsx';
import {Button} from 'react-bootstrap';
import {FaTimes, FaMinusCircle, FaPlusCircle} from 'react-icons/fa';

// Item Template Data
// {
//     "id" : 1,
//     "name" : "Blue Dream 1/8 oz",
//     "description" : "Hybrid",
//     "longDescription": "This high quality leaf is meant to give you a calm, relaxing high. This will give you a focused yet calm high throughout the day.",
//     "measurement" : "24% THC",
//     "price" : 19.99,
//     "type" : "flower",
//     "imageURL" : "https://media.phillyvoice.com/media/images/01_030817_Dispensary_Carroll.2e16d0ba.fill-735x490.jpg"
//   },

const CartItem = (itemData, setCartItems, updateQuantity, removeProduct) => {
    const decrementItem = () => {
        if(itemData.quantity>0){
            updateQuantity(-1,itemData.id);
        }
    }
    const incrementItem = () => {
        if(itemData.quantity>=0){
            updateQuantity(1,itemData.id);
        }
    }
    return(
      <ListItem>  
        <RedIconWrapper onClick={()=>{removeProduct(itemData.id)}}>
            <FaTimes />
        </RedIconWrapper>
        <CartImage src = {itemData.imageURL} />
        <InfoContainer>
            {itemData.name}
            <br />
            {itemData.measurement}
            <br />
            {`Quantity: `+itemData.quantity}
            <br />
            {`Price: $`+itemData.price.dollars + `.` + (itemData.price.cents<10 ? `0` :``) + itemData.price.cents}
        </InfoContainer>
        <RedIconWrapper>
            <FaMinusCircle onClick={()=>{decrementItem()}}/>
        </RedIconWrapper>
        <GreenIconWrapper>
            <FaPlusCircle onClick={()=>{incrementItem()}}/>
        </GreenIconWrapper>
      </ListItem>
    )
};

const getCartItems = (items, cartCount, cartTotal, setCartItems, updateQuantity, removeProduct, checkout ) => {
    return(
        <Fragment>
        <CartTitle> My Cart </CartTitle>
        <CartList>
        {
            Object.keys(items).map(key=>CartItem(items[key], setCartItems, updateQuantity, removeProduct))
        }
        </CartList>
        <CartTotalContainer>
            Total Number of items: {cartCount}
            <br/>
            Tax:
            <br/>
            Delivery Fee:
            <br/>
            {`Total Cost: $${cartTotal.dollars}.` + (cartTotal.cents<10 ? `0` : ``) + `${cartTotal.cents}`}
            <br/>
            <br/>
            <Button variant="success" disabled={(cartCount === 0)} onClick={() => {checkout()}}>Place Order</Button>  
        </CartTotalContainer>
        
        </Fragment>
    );
}

export default CartItem;
export {getCartItems};
