import React, {Fragment, useState} from 'react';
import {
    OrderTitle, OrderSubtitle, InfoContainer, ContactInfoContainer, 
    OrderInfoContainer, ErrorText
} from '../components-jsx/ConfirmOrder.jsx';
import { 
    SignUpContainer
} from '../jsx/Confirm.jsx';
import {Button} from 'react-bootstrap';

// Item Template Data
// {
//     "id" : 1,
//     "name" : "Blue Dream 1/8 oz",
//     "description" : "Hybrid",
//     "longDescription": "This high quality leaf is meant to give you a calm, relaxing high. This will give you a focused yet calm high throughout the day.",
//     "measurement" : "24% THC",
//     "price" : 19.99,
