const ConfirmOrder = (cartCount, cartTotal, confirmOrder, submitOrder, errorMessage, userData, orderConfirmed, confirmComplete) => {
    let name = "";
    let tel = "";
    if(orderConfirmed){
        return(
            <Fragment>
                 <OrderTitle>Order Confirmed!</OrderTitle>
                 <br/>
                 <OrderSubtitle>
                    Thank you for shopping with us!
                    <br/>
                    <br/>
                    <Button variant="success" onClick={() => {confirmComplete()}}>Back to Store</Button>
                 </OrderSubtitle>
                 
            </Fragment>
        )
    }
    return(
        <Fragment>
            <OrderTitle> Confirm Order </OrderTitle>
            <ContactInfoContainer>
                <span>Contact Info</span>
                <br/>
                <br/>
                <SignUpContainer>
                    <input
                        type={"text"}
                        placeholder={"First and Last Name"}
                        maxLength={45}
                        onChange={(e)=>{name = e.target.value}}
                    />
                </SignUpContainer>
                <SignUpContainer>
                    <input
                        type={"tel"}
                        placeholder={"Phone"}
                        maxLength={11}
                        onChange={(e)=>{tel = e.target.value}}
                    />
                </SignUpContainer>
                <br/>
                <br/>
            </ContactInfoContainer>
            <OrderInfoContainer>
                Total Number of items: {cartCount}
                <br/>
                Tax:
                <br/>
                Delivery Fee:
                <br/>
                {`Total Cost: $${cartTotal.dollars}.` + (cartTotal.cents<10 ? `0` : ``) + `${cartTotal.cents}`}
                <br/>
                <br/>
                <Button variant="success" onClick={() => {submitOrder({name, tel})}}>Submit Order</Button>
                <br />
                <br />
                <ErrorText>{errorMessage}</ErrorText>
                
            </OrderInfoContainer>
        
        </Fragment>
    );
}

export default ConfirmOrder;
